<script>
    import Hangul from 'hangul-js';
    import categoryList from "./category-list.js";
	import CopyClipBoard from './CopyClipBoard.svelte';

    let inputText = '';
    let selected_category = '';
    let category_number = '';
	let cat_msg = '';
	const copy = () => {
		const app = new CopyClipBoard({
			target: document.getElementById('cat_clipboard'),
			props: { 'source_columns':category_number },
		});
		cat_msg = selected_category + '의 카테고리 번호가 클립보드에 복사되었습니다.';
		setTimeout(() => {cat_msg = '';}, 15000);
		app.$destroy();
	}

    function matchedWords( searchText ) {
        const searcher = new Hangul.Searcher(searchText);
        const output = [];
        let search_v = -1;
        categoryList.forEach( word => {
            search_v = searcher.search(word);
            if (search_v >= 0) {
                output.push({ text: word, id: search_v });
            }
        })
        output.sort(function (a,b){ return a.text < b.text ? -1 : a.text > b.text ? 1 : 0; });
        output.sort(function (a,b){ return a.id - b.id });
        return output;
    }
    function makeBold( targetWord ) {
        let shift = 0;
        const strArray = targetWord.split('');
        const boldPoints = Hangul.rangeSearch(targetWord, inputText);
        boldPoints.forEach( indexs => {
            strArray.splice(indexs[0]+(shift++),0,'<b style="color:#1E94FC;">');
            strArray.splice(indexs[1]+1+(shift++),0,'</b>');
        })
        return `${strArray.join('')}`;
    }
    function copyCategoryNumber( theCategoryText ) {
        selected_category = theCategoryText;
        category_number = '' + parseInt(theCategoryText);
        copy();
    }
</script>

<div class="content">
    <div id="search_div">
        카테고리 검색
        <input bind:value={inputText}>
		<div id="cat_clipboard"></div>
        <span class="cat_msg">{cat_msg}</span>
    </div>
    <div>
        {#each matchedWords(inputText) as matched_word}
            {@html makeBold(matched_word.text)} <span class="copy_span" on:click={copyCategoryNumber(matched_word.text)}>#</span><br>
        {/each}
    </div>

</div>

<style>
    #search_div {
        position: -webkit-sticky;
        position: sticky;
        top: 0px;
    }
    #cat_clipboard {
        display: inline-block;
    }
    input {
        width: 300px;
		color: white;
		background: rgb(58, 56, 56);
    }
    .copy_span {
		color: yellow;
        cursor: pointer;
    }
    .cat_msg {
        color: blueviolet;
    }
</style>
