<script>
	import CategorySearch from './CategorySearch.svelte';
	import CopyClipBoard from './CopyClipBoard.svelte';

	let msg = '';

	const copy = () => {
		const app = new CopyClipBoard({
			target: document.getElementById('clipboard'),
			props: { source_columns },
		});
		msg = '수정사항 목록이 클립보드에 복사되었습니다.';
		setTimeout(() => {msg = '';}, 3000);
		app.$destroy();
	}

	const page_title = '카테고리 매핑';
    const naver_shopping_search_query = 'https://search.shopping.naver.com/search/all?query=';
    let naver_shopping_search_url = '';
	let source_columns = '';
    let inputText = '';

	$: {
        naver_shopping_search_url = naver_shopping_search_query + inputText;
    }
	</script>
	
	<svelte:head>
		<title>{page_title}</title>
	</svelte:head>
	
	<div>
		<button on:click={copy}>수정사항 목록 클립보드로 복사하기</button> {msg}
		<div id="clipboard"></div>
	</div>
	<div>
        네이버 쇼핑에서 검색하기 <input bind:value={inputText}>
        <a href='{naver_shopping_search_url}' target="_blank"><img src='./naver_shopping.png' alt='네이버 쇼핑에서 검색하기'></a>
	</div>
	<div>
		<div class="flex">
			<textarea bind:value={source_columns}></textarea>
		</div>
		<div class="flex">
			<CategorySearch />
		</div>
	</div>
	
	<style>
	:global(body) {
		background: rgb(32, 31, 31);
		color: white;
	}
	textarea {
		width: 600px;
		height: 600px;
		color: white;
		background: rgb(58, 56, 56);
	}
	.flex {
		display: inline-block;
		vertical-align: top;
	}
    input {
        width: 300px;
		color: white;
		background: rgb(58, 56, 56);
    }
    img {
        height: 30px;
        margin-top: 0px;
        margin-bottom: 2px;
        vertical-align: middle;
    }
	</style>
	